<template>
    <div class="app">
        <ul class="ov treeBox">
            <li class="fl"><a href="javascript:void (0)" @click="navNum = 1" :class="{active:navNum == 1}" class="pr">财富树总收入<span></span></a></li>
            <li class="fl"><a href="javascript:void (0)" @click="navNum = 2" :class="{active:navNum == 2}"  class="pr">财富树推广收入<span></span></a></li>
        </ul>
        <div v-show="navNum == 1">
            <div class="incomeBox">
                <div class="titleInfo">
                    <div class="topBox ov">
                        <p class="fl left"><img :src="currency.icon" alt=""><span>{{currency.name}}</span></p>
                        <p class="fr right" @click="goCurrency"><span>币种切换</span><img src="@/static/images/my/qiehuan.png" alt=""></p>
                    </div>
                    <div class="bottomBox ov">
                        <div class="fl left"><p>总共支付升级费用</p><span>{{headData.expense}}</span></div>
                        <div class="fl right"><p>总共收取下级升级费用</p><span>{{headData.income}}</span></div>
                    </div>
                </div>
                <div class="holderNum pr"><p>财务流水记录</p></div>
            </div>
            <div class="art"><p>这里的财务流水是统计您参与的所有财富树的流水情况</p></div>
            <ul class="flowBox">
                <van-list
                        v-model="loading"
                        :finished="finished"
                        finished-text="没有更多了~"
                        @load="getTreeLog"
                        v-if="listData.length"
                >
                    <li v-for="(item, index) in listData" :key="index">
                        <a href="javascript:void(0);" class="ov">
                            <p class="fl ov"><img class="fl" :src="item.userinfo.avatar" alt=""><em class="fl"><span>{{item.userinfo.nickname}}</span><i class="time">{{item.datetime}}</i></em></p>
                            <p class="fl"><span>财富树密码</span><i>{{item.room_number}}</i></p>
                            <p class="fl"><span>+{{item.money}}</span><i>{{item.remark}}</i></p>
                        </a>
                    </li>
                </van-list>
            </ul>
        </div>
        <user-box v-show="navNum == 2" :currency="currency_id"></user-box>
    </div>
</template>

<script>
    import { currencyFirst, currencyInfo} from "@/api/index";
    import { treeLog, treeDis } from "@/api/user";
    import { List } from "vant"
    import 'vant/lib/list/style'
    import userBox from "@/page/mine/red/components/user"
    export default {
        name: "Treelog",
        components: {
            userBox,
            [List.name]: List,
        },
        data()
        {
            return {
                navNum:1,
                page:1,
                loading:false,
                finished:false,
                //高亮
                activeNav:0,
                // 币种id
                currency_id:'',
                // 币种信息
                currency:{
                    name:'',
                    tag:'',
                    icon:''
                },
                headData:[],
                listData:[]

            }
        },
        created()
        {
            this.pageInit()
        },
        mounted()
        {
            this.getTreeLog()

        },
        methods:{

            async pageInit()
            {

                this.currency_id = this.$route.query.currencyId

                /** 处理一下 默认先为1 */
                this.currency_id  =  this.currency_id ?  this.currency_id : 1;
                if (this.currency_id)
                {
                    let res = await currencyInfo(this.currency_id)
                    this.currency = res

                } else
                {
                    let res = await currencyFirst()
                    this.currency_id = res.id
                    this.currency = res
                }


            },

            // 加载数据
            getTreeLog()
            {
                this.loading = true
                treeLog({currency_id:this.currency_id, page:this.page}).then(res => {
                    this.loading = false
                    if (this.page == 1)
                    {
                        this.headData = res.head
                    }
                    this.page++
                    if (res.list.length < 20)
                    {
                        this.finished = true
                    }
                    this.listData = this.listData.concat(res.list)
                })
            },
            changeNav(num)
            {
                this.activeNav = num
            },
            goCurrency()
            {
                this.$router.push({path:'/currency', query:{redirect:'/treelog'}})
            },

        }
    }
</script>

<style scoped>
    @import "../../../static/css/mtreelog.css";
</style>



